import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Container from './components/container';
import NavBar from './components/navBar';
import BottomBar from './components/bottomBar';
import Writer from './components/writer';
import * as serviceWorker from './serviceWorker';
import SocialBox from './components/socialBox';

let nodesList = [];

fetch('http://aiscstudents.com/api/getMaxArticleId/', {
    method: 'GET',
    credentials: 'include'
}).then((response) => {
    if (response.status === 301) {
        console.log(response)
        //window.location.href = "http://aiscstudents.com/login";
        return;
    }
    response.json().then((data) => {
        fetch('http://aiscstudents.com/api/whoami/', {
            method: 'GET',
            credentials: "include",
            headers: {
                'Access-Control-Allow-Origin': "aiscstudents.com"
            }
        }).then((response2) => {
            if (response2.status === 301) {
                console.log(response)
                window.location.href = "http://aiscstudents.com/login";
                return;
            }
            response2.json().then((hashJSON) => {
                const hashID = hashJSON['hashID'];
                for (let i = 0; i < data.length; i++) {
                    nodesList.push(<Container articleId={data[i]['id']} hashID={hashID} />)
                }
                ReactDOM.render(<React.StrictMode><SocialBox hashID={hashID} /><NavBar />{nodesList}<Writer writer="tempWriter" /></React.StrictMode>, document.getElementById('root'));
            })
        })
    }
    )})
serviceWorker.unregister();
//<Writer /><NavBar />
